import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { gzip, ungzip, pako } from 'pako';
import { Observable, BehaviorSubject } from 'rxjs';
declare var Zlib: any;
import * as _ from 'lodash';

export class Result {
  public Contents: any;
  public Response: string;
}

@Injectable({
  providedIn: 'root'
})
export class GameService {
  basicInfo$: BehaviorSubject<any> = new BehaviorSubject<any>(null);
  gameInfo$: BehaviorSubject<any> = new BehaviorSubject<any>(null);
  userInfo$: BehaviorSubject<any> = new BehaviorSubject<any>(null);

  start$: BehaviorSubject<any> = new BehaviorSubject<any>(false);


  //apiUrl = 'http://localhost:3000/tap-game';
  apiUrl = '/tap-game';
  updateUrl = 'assets/update.json';
  userUrl = 'assets/user.json';
  prestigeUrl = 'assets/prestige.json';

  prestigeCount = 0;
  // tslint:disable-next-line: max-line-length
  playGameData = 'H4sIAAAAAAAAEz3NsQ6CMBSF4Xe5MyXccinVzUiiJm6iJm63UJGBkrRlMMZ3tyyO58tJ/g80HPlmfRhnB1ssMjizGxYeLGzh8RJHdhEy+D8A8yrXSfbz4qJ/J2nvaR54spfIPq7gF5uouZ6atLiWRa9UL8qnkYKINkJXPQqjamMsIqmOYM2GuOs6G0I7TilOVFCdV1KXUiOW9P0BdghRpKsAAAA=';
  constructor(private http: HttpClient) {
  }




  getBasicInfo() {
    this.http.get(`${this.apiUrl}/basicInfo`).subscribe((result: Result) => {
      this.basicInfo$.next(result.Contents);
    });
  }

  getGameInfo() {
    this.http.get(this.updateUrl).subscribe(result => {
      this.gameInfo$.next(result);
    });
  }

  getUserInfo() {
    this.http.get(this.userUrl).subscribe((result: Result) => {
      this.userInfo$.next(result.Contents);
    });
  }

  playGame() {
    const data = {
      config: this.playGameData,
      token: this.basicInfo$.getValue().Token
    };

    this.http.post(`${this.apiUrl}/playGame`, data).subscribe((result: Result) => {

      const updateData = {
        Heroes: result.Contents.Heroes,
        ClearStage: result.Contents.DeviceSync.ClearStage,
        Medal: result.Contents.Medal
      };


      this.updateGame(updateData);

      this.start$.next(true);
    });
  }

  updateGame(data) {
    const newGameInfo = _.assign(this.gameInfo$.value, data);
    this.gameInfo$.next(newGameInfo);
  }

  update(data) {
    this.http.post(`${this.apiUrl}/update`, data).subscribe(result => {
      console.log(result);
    }, err => {
      this.getBasicInfo();
      setTimeout(() => {
        this.playGame();
      }, 3000);
    });
  }


  prestige() {
    this.http.get(this.prestigeUrl).subscribe((result: Result) => {

      const req = {
        token: this.basicInfo$.getValue().Token,
        data: _.assign(result, { ClearStage: this.gameInfo$.value.ClearStage })
      };

      this.http.post(`${this.apiUrl}/Prestige`, req).subscribe(prestigeResult => {
        if (prestigeResult) {
          this.prestigeCount += 1;
          this.playGame();
        }
      });
    });
  }



}
