import { Component, OnInit } from '@angular/core';
import { gzip, ungzip } from 'pako';
import { GameService } from './game.service';
import { BehaviorSubject, of, Observable } from 'rxjs';
import * as _ from 'lodash';
import { take, map } from 'rxjs/operators';
import * as moment from 'moment';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'tap-defenders';
  displayedColumns: string[] = ['Index', 'Name', 'Level', 'MaxLevel', 'Feature'];

  // tslint:disable-next-line: max-line-length
  zz = { DataVersion: 10, Language: 'Zh-Hant', Version: '1.5.8', Country: 'TW', GameStart: 'True', DUID: 'a720d66d-3fb2-4449-85d1-b67bbe1146c4', LastAccessTime: 44043.5329359259 };

  basicInfo$: BehaviorSubject<any>;
  gameInfo$: BehaviorSubject<any>;
  userInfo$: BehaviorSubject<any>;

  dataList$: Observable<Array<any>>;
  heroList$: Observable<Array<any>>;

  updateOption = {
    updateTime: 25,
    disable: true,
    status: false
  };

  timeout: any;
  timeoutByGameUpdateMessage: string;
  currentDate = new Date(new Date().getTime() + new Date().getTimezoneOffset() * 60000);

  ClearStage = 7950;
  constructor(
    public gameService: GameService
  ) {
    this.basicInfo$ = this.gameService.basicInfo$;
    this.gameInfo$ = this.gameService.gameInfo$;
    this.userInfo$ = this.gameService.userInfo$;


  }

  ngOnInit() {
    this.gameService.getGameInfo();
    this.gameService.getUserInfo();

    this.gameService.start$.subscribe(result => {
      if (result) {
        this.update();
        this.gameService.start$.next(false);
      }
    });

    // this.getBasicInfo();
    this.gameInfo$.subscribe(result => {
      if (result) {
        console.log(result.Heroes);

        this.heroList$ = this.defaultData(result.Heroes);
      }
    });

    setInterval(() => {
      this.currentDate = new Date(new Date().getTime() + new Date().getTimezoneOffset() * 60000);
    }, 1000);
  }

  getBasicInfo() {
    this.gameService.getBasicInfo();
  }

  playGame() {
    this.gameService.playGame();

    setTimeout(() => {
      this.update();
      console.log('開始');
    }, 5000);
  }

  /*  playGame() {
     const newGameInfo = _.assign(this.gameInfo$.value, { Heroes: this.userInfo$.value.Heroes });
     this.gameInfo$.next(newGameInfo);
   } */

  updateGame(data) {
    const newGameInfo = _.assign(this.gameInfo$.value, data);
    this.gameInfo$.next(newGameInfo);
  }

  keyByArray(data) {
    return Object.keys(data);
  }

  defaultData(data) {
    const dataList = Object.keys(data).map((v, index) => {

      return _.assign(data[v], {
        Index: index + 1,
        Name: v,
        Status: false
      });
    });

    return of(dataList);
  }

  update() {
    let second = 0;

    window.clearInterval(this.timeout);

    this.timeout = setInterval(() => {
      second += 1;

      this.timeoutByGameUpdateMessage = `${this.updateOption.updateTime - second} s`;

      if (this.updateOption.updateTime === second) {
        this.updateFunc();
        second = 0;
      }


      console.log(this.updateOption.updateTime - second);
    }, 1000);
  }

  updateTime(time) {
    // tslint:disable-next-line: radix
    this.updateOption.updateTime = parseInt(time);

    if (this.updateOption.status) {
      this.update();
    } else {
      window.clearInterval(this.timeout);
    }

    console.log(time);
  }

  stop() {
    window.clearInterval(this.timeout);
  }

  updateFunc() {
    this.timeoutByGameUpdateMessage = 'Updating ...';
    console.log('------------FK------------');
    const updateGame = {
      DeviceTime: this.toOADate(this.currentDate),
      ClearStage: this.gameInfo$.value.ClearStage + this.updateOption.updateTime * 4
    };

    const req = {
      token: this.basicInfo$.getValue().Token,
      data: this.gameInfo$.value
    };

    console.log(updateGame);
    if (this.gameInfo$.value.ClearStage + this.updateOption.updateTime * 4 > this.ClearStage) {

      this.stop();
      this.prestige();
    } else {
      this.updateGame(updateGame);
      this.gameService.update(req);
    }


  }


  prestige() {
    this.gameService.prestige();
  }



  filterPage(list, paginator) {
    return _.chunk(list, paginator.pageSize)[paginator.pageIndex];
  }

  toOADate(date) {
    const timezoneOffset = date.getTimezoneOffset() / (60 * 24);
    const msDateObj = (date.getTime() / 86400000) + (25569 - timezoneOffset);
    return msDateObj;
  }

  fromOADate(oaDate) {
    const date = new Date(((oaDate - 25569) * 86400000));
    const tz = date.getTimezoneOffset();
    return new Date(((oaDate - 25569 + (tz / (60 * 24))) * 86400000));
  }

  newDate() {
    return new Date();
  }

  mathFloor(value) {
    return  Math.floor(value / 1000000000000);
  }
}
